<template>
  <div>
    <div class="actions">
      <button class="button" @click="update">Zapisz zmiany</button>
      <button class="button" @click="remove">Usu≈Ñ</button>
    </div>
    <div>
      <label>Nazwa
        <input class="input" type="text" v-model="groupName"/>
      </label>
      <label>Data pobrania
        <span :style="takenStyle" class="taken">
            ({{taken}} dni temu)
        </span>
        <input class="input" type="date" v-model="group.dateTaken"/>
      </label>
      <label>Dzielnik
        <input class="input" type="number" v-model="group.divider"/>
      </label>
    </div>
    <ul>
      <group-item
        v-for=" (value, key, index) in group.items"
        :key="key"
        :value="value"
        :dbKey="key"
        :name="groupName"
        :index="index"
        :divider="group.divider"
      >
      </group-item>
    </ul>
  </div>
</template>

<script>
import GroupItem from '@/components/EditItem.vue';

export default {
  components: {
    GroupItem,
  },
  name: 'edit',
  computed: {
    takenStyle() {
      if (this.taken > 90) {
        return {
          color: 'red',
        };
      }
      return { color: '#e67f0d' };
    },
  },
  data() {
    return {
      group: {},
      groupName: '',
      taken: '',
    };
  },
  methods: {
    update() {
      const oldName = this.$route.params.name;
      if (this.groupName !== oldName) {
        this.$store.dispatch('deleteGroup', oldName);
      }

      this.$store.dispatch('updateGroup', {
        name: this.groupName,
        data: this.group,
      });
    },
    remove() {
      if (confirm('Czy na pewno usunƒÖƒá?')) { // eslint-disable-line
        this.$store.dispatch('deleteGroup', this.groupName);
      }
      this.$routet.go(-1);
    },
    countTaken() {
      const now = Date.now();
      const taken = new Date(this.group.dateTaken).getTime();
      this.taken = Math.floor((now - taken) / 86400000);
    },
  },
  created() {
    this.groupName = this.$route.params.name;
    this.group = this.$store.getters.group(this.groupName);
    this.countTaken();
  },
};
</script>

<style lang="scss" scoped>
  .actions {
    margin: 1em 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
</style>
