<template>
  <li
    v-if="value.x || value.o"
    class="list-item"
  >
    <span>
      {{value.id}}
    </span>
    <button
      v-if="value.x"
      class="x button button--circle"
      @click="disable()"
    >
      X
    </button>
    <button
      v-if="value.o"
      class="o button button--circle"
      @click="success()"
    >
      O
    </button>
  </li>
</template>

<script>

export default {
  props: [
    'value',
    'dbKey',
    'name',
    'index',
  ],
  methods: {
    success() {
      this.$store.dispatch('update', {
        name: this.name,
        key: this.dbKey,
        data: { o: !this.value.o },
      });
    },
    disable() {
      this.$store.dispatch('update', {
        name: this.name,
        key: this.dbKey,
        data: { x: !this.value.x },
      });
    },
  },
};
</script>
